import './Navbar.css'
import Logo from '../../images/logo.png'
import RightArrow from '../../images/arrow.png'
import Grid from '../../images/grid.png'
import React, { useState, useEffect } from 'react';
import {Link} from "react-router-dom"
import Buttons from '../Buttons/Buttons';


const Navbar = () => {

  const [matches, setMatches] = useState(
    window.matchMedia("(min-width: 1024px)").matches
  )
  useEffect(() => {
    window
    .matchMedia("(min-width: 1024px)")
    .addEventListener('change', e => setMatches( e.matches ));
  }, []);

const [show, setShow] = useState(false);

// console.log(show)

// { window.matchMedia("(min-width: 1024px)").matches && setShow(true) }

const linkClick = ()=>{
  show && setShow(false)
  console.log(show)
}
const imageClick = ()=>{
  show ? setShow(false) : setShow(true)
  console.log(show)
}
    return (
      <div className="navbar" id="navbar">

{(() => {
        if (matches) {
          return (
            <div className="navbar_content">
            <Link to="/">
            <img src={Logo} alt="" />
            </Link>
              <div className="nav_links" 
              // style={{display: show}}
              >
                <ul>
                  <li>
                    <Link to="/">Home</Link>
                  </li>
                  <li>
                    <Link to="/services">Services</Link>
                  </li>
                  <li>
                    <Link to="/works">Our Works</Link>
                  </li>
                  <li>
                  <Link to="/about-us">About Us</Link>
                </li>
                </ul>
                <div className="quote">
                  <Link to="/">GET QUOTE</Link>
                  <img src={RightArrow} alt="" />
                </div>
              </div>
            </div>
          );
        } else {
          return (
        <div className="navbar_content">
          <Link to="/" onClick={() => linkClick()}>
            <img src={Logo} alt="" />
            </Link>
          <img src={Grid} className="dropdown" onClick={() => imageClick()} />
            <div className="nav_links" style={{display: show && 'block'}}
            >
              <ul>
                <li>
                  <Link to="/" onClick={() => linkClick()}>Home</Link>
                </li>
                <li>
                  <Link to="/services" onClick={() => linkClick()}>Services</Link>
                </li>
                <li>
                  <Link to="/works" onClick={() => linkClick()}>Our Works </Link>
                </li>
                <li>
                  <Link to="/about-us" onClick={() => linkClick()}>About Us</Link>
                </li>
              </ul>
              <div className="quote" >
                <Link to="/contact-us" onClick={() => linkClick()}>GET QUOTE</Link>
                <img src={RightArrow} alt="" />
              </div>
              {/* <Buttons 
              onClick={() => linkClick()}
              link="contact-us"
              value="GET QUOTE"
              image={RightArrow}
              buttontype="btn small_btn black_btn"
              /> */}
            </div>
        </div>
          );
        }
      })()}




      </div>
    );
}
 
export default Navbar;
